import React, { useState, useMemo, useRef, useEffect } from 'react';
import {
  MapControls,
  OrbitControls,
} from 'three/examples/jsm/controls/OrbitControls';

import { extend, useFrame } from 'react-three-fiber';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { useSpring, animated } from 'react-spring/three'
import { navigate } from "gatsby"
import path from 'path'
extend({
  MapControls,
  OrbitControls,
});

export const distance = 1600;

// 
export const Outer = () => {
  return ImportGltf('/gallaxy/scene.gltf', '', 'outer', true);
};
// 
export const PlanetEarth = ({ categorySet, hasPortfolio } ) => {
    return ImportGltf('/planet_earth/scene.gltf', '', 'planetearth', true, [0,0,0], [2, 2, 2], categorySet, hasPortfolio);
};
// 
export const PlanetMars = ({ categorySet, hasPortfolio } ) => {
    return ImportGltf('/mars/scene.gltf', '', 'mars', true, [30, 10, 50], [0.05,0.05,0.05], categorySet, hasPortfolio );
};

// 
export const PlanetMercury = ({ categorySet, hasPortfolio } ) => {
    return ImportGltf('/venus/scene.gltf', '', 'mercury', true, [20, -20, -50], [0.08,0.08,0.08], categorySet, hasPortfolio);
};
export const ImportGltf = (URL, name, tag, rotate, position, scale, categorySet, hasPortfolio) => {
    const [model, setModel] = useState();
    useMemo(
        () =>
        new GLTFLoader().load(URL, (e) => {
            setModel(e);
        }),
        [URL],
    );

    const [active, setActive] = useState(false)
    const [hovered, setHover] = useState(false)

    const { color, pos, ...props } = useSpring({
      color: active ? 'hotpink' : 'white',
      pos: active ? [0, 0, 2] : [0, 0, 0],
      'material-opacity': hovered ? 0.6 : 0.25,
      scale: hovered ? [1.2, 1.2, 1.2] : [1, 1, 1],
      config: { mass: 10, tension: 1000, friction: 300, precision: 0.00001 }
    })
    const tooltip = document.querySelector("#tooltip")
    const onPointHover = (e) => {
        setHover(true)
        tooltip.classList.add("isvisible")
        if (tag === 'planetearth'){
            tooltip.innerHTML = `
                <h1>JIGGLYPOP</h1>
                <h1></h1>
                <h3> .    ,   .</h3>
                <h3>     VUE, ANGULAR  .</h3>
                <h3>    .</h3>
                <h3>3D        .</h3>
                <h2>    .</h2>
            `
        } else if (tag === 'mars'){
            tooltip.innerHTML = `
                <h1></h1>
                <h3>  .</h3>
                <h3>   .</h3>
                <h2>   .</h2>
            `
        } else if (tag === 'mercury'){
            tooltip.innerHTML = `
                <h1></h1>
                <h3>${hasPortfolio && hasPortfolio.map(item => {
                    return `${item.title} `
                })}</h3>
                <h2>   .</h2>
            `
        }
    }
    const onPointOut = (e) => {
        setHover(false)
        tooltip.classList.remove("isvisible")
    }

    const onClick = (e) => {
        if (tag === 'planetearth'){
            navigate("/resume");
        } else if (tag === 'mars'){
            navigate("/pages/1");
        } else if (tag === 'mercury'){
            navigate("/portfolios");
        }
    }
    const mesh = useRef()
    useFrame((state, delta) => (mesh.current.rotation.y += 0.001))
    return (
        <group>
            <animated.mesh 
                onClick={e => onClick(e)} onPointerOver={e => onPointHover(e)} onPointerOut={e => onPointOut(false)} {...props}>
                <mesh position={position ? position :[0, 0, 0]} scale={scale ? scale :[1, 1, 1]} ref={mesh} receiveShadow castShadow>
                    <rectAreaLight
                        width={10}
                        height={10}
                        intensity={2000}
                        color={'white'}
                        position={[100,100,100]}
                        rotation={[0, 180, 0]}
                        castShadow
                        />
                    {model && <primitive object={model.scene} receiveShadow castShadow />}
                </mesh>
            </animated.mesh>
        </group>

    );
};
